                                    const simpleEndX = cx +
                                        startNoteRenderer.x +
                                        startNoteRenderer.getBeatX(this._beat, BeatXPosition.PostNotes) +
                                        2;
                                    const middleX = (simpleStartX + simpleEndX) / 2;
